<template>
	<q-page padding>
		<div class="param-form" >
			<q-form @submit="setGetlocation">
				<div class="q-gutter-md">
					<div class="text-h4">找附近餐廳</div>
					<q-badge color="secondary">
						半徑: {{ radius }} 公尺
					</q-badge>
					<div style="width: 90%; margin: 25px;">
						<q-slider
						v-model="radius"
						:min="min"
						:max="max"
						:step="100"
						snap
						label
						label-always
						color="purple"
					/>
					</div>
					<q-btn :loading="loading" label="找餐廳" type="submit" color="primary" style="width: 150px">
						<template v-slot:loading>
							<q-spinner-hourglass class="on-left"></q-spinner-hourglass>
							Loading...
						</template>
					</q-btn>
				</div>
			</q-form>
		</div>
		<!-- <list-restaurants :radius="radius" /> -->
		<infinite-scroller @processingLoad="onLoad"/>
	</q-page>
</template>

<script>
import { mapGetters } from 'vuex';
import { filter, takeUntil } from 'rxjs/operators';
import { timer } from 'rxjs';
import InfiniteScroller from '@/components/InfiniteScroller.vue';

export default {
	name: 'home',
	components: {
		// HelloWorld,
		// ListRestaurants,
		InfiniteScroller,
	},
	data() {
		return {
			radius: 500,
			max: 1000,
			min: 100,
			loading: false,
		};
	},
	computed: {
		...mapGetters(['getGeolocation', 'getNextPageToken']),
	},
	methods: {
		fetchRestaurantsList() {
			const onSuccess = () => {
				console.log('test123, onSuccess');
				this.loading = false;
			};
			const payload = {
				location: this.getGeolocation,
				radius: this.radius,
				type: 'restaurant',
				onSuccess,
			};
			this.$store.commit('cleanList');
			this.$store.dispatch('fetchRestaurantList', payload);
		},
		setGetlocation() {
			console.log('test123 setGetlocation');
			this.loading = true;
			this.$getLocation().then((coordinates) => {
				console.log('coordinates', coordinates);
				const getlocation = {
					latitude: coordinates.lat,
					longitude: coordinates.lng,
				};
				this.$store.commit('setGetlocation', getlocation);
				localStorage.setItem('hasGeolocationPermission', true);
				this.fetchRestaurantsList();
			});
		},
		onLoad(done) {
			const timer$ = timer(100, 1000);
			const stop$ = timer$.pipe(filter(
				() => { return Object.keys(this.getGeolocation).length > 0; },
			));
			const result$ = timer$.pipe(takeUntil(stop$));
			this.$subscribeTo(
				result$,
				null,
				null,
				() => {
					const onSuccess = (hasNext = true) => {
						console.log('has next', hasNext);
						done(!hasNext);
					};
					if (this.getNextPageToken) {
						console.log('fetch next');
						this.$store.dispatch('fetchNextPage', onSuccess);
					} else {
						console.log('first fetch');
						const payload = {
							location: this.getGeolocation,
							radius: this.radius,
							type: 'restaurant',
							onSuccess,
						};
						this.$store.dispatch('fetchRestaurantList', payload);
					}
				},
			);
		},
	},
	mounted() {
		this.$store.commit('setCurrentPath', 'home');
		this.$store.commit('setParentPath', '/');
	},
};
</script>
