const state = {
	geolocation: {},
	currentPagination: 1,
	currentPath: null,
	parentPath: null,
};

const mutations = {
	setGetlocation(state, payload) {
		state.geolocation = payload;
	},
	setCurrentPagination(state, payload) {
		state.currentPagination = payload;
	},
	setCurrentPath(state, payload) {
		state.currentPath = payload;
	},
	setParentPath(state, payload) {
		state.parentPath = payload;
	},
};

const actions = { };

const getters = {
	getGeolocation: (state) => { return state.geolocation; },
	getCurrentPagination: (state) => { return state.currentPagination; },
	getCurrentPath: (state) => { return state.currentPath; },
	getParentPath: (state) => { return state.parentPath; },
};

export default {
	state,
	mutations,
	actions,
	getters,
};
