<template>
	<q-layout view="lHh LpR fFf" class="meal">
		<q-header reveal elevated class="bg-primary text-white">
			<q-toolbar>
				<q-btn
					dense
					flat
					round
					icon="menu"
					@click="leftDrawerOpen = !leftDrawerOpen"
				/>
				<q-toolbar-title>
					<q-avatar>
						<img
							src="https://cdn.quasar.dev/logo/svg/quasar-logo.svg"
						/>
					</q-avatar>
					吃啥
				</q-toolbar-title>
				<q-btn
					flat
					round
					icon="my_location"
					@click="setCurrentLocation"
					v-show="noPermission"
				>
				</q-btn>
				<div>
					<q-tooltip v-model="noPermission" self="top left">
						{{tooltipMessage}}
					</q-tooltip>
				</div>
			</q-toolbar>
		</q-header>

		<q-drawer
			v-model="leftDrawerOpen"
			side="left"
			behavior="mobile"
			:width="200"
			elevated
		>
			<q-list padding>
				<q-item-label header>Menu</q-item-label>
				<q-item clickable @click="goTo('home')"
				v-ripple :active="currentSelected === 'home'"
				active-class="my-menu-link">
					<q-item-section avatar>
						<q-icon name="home"/>
					</q-item-section>
					<q-item-section>
						<q-item-label>Home </q-item-label>
					</q-item-section>
				</q-item>
				<q-item clickable
				@click="goTo('search')"
				v-ripple :active="currentSelected === 'search'"
				active-class="my-menu-link">
					<q-item-section avatar>
						<q-icon name="search" />
					</q-item-section>
					<q-item-section>
						<q-item-label>Search</q-item-label>
					</q-item-section>
				</q-item>
			</q-list>
		</q-drawer>

		<q-page-container>
			<router-view />
		</q-page-container>
	</q-layout>
</template>

<script>
// import HelloWorld from './components/HelloWorld.vue';
// import { mapGetters } from 'vuex';
import { from } from 'rxjs';
import { map } from 'rxjs/operators';
import { mapGetters } from 'vuex';

export default {
	name: 'LayoutDefault',
	components: {},
	data() {
		return {
			leftDrawerOpen: false,
			noPermission: true,
			hasGeolocationPermission: false,
			tooltipMessage: '需要取得當前位置權限',
			currentSelected: 'home',
		};
	},
	methods: {
		setCurrentLocation() {
			this.tooltipMessage = '需要一點時間..';
			const location$ = from(this.$watchLocation()).pipe(
				map((coordinates) => {
					console.log('get coordinates');
					return coordinates;
				}),
			);
			this.$subscribeTo(
				location$,
				(coordinates) => {
					this.setGeolocation(coordinates);
				},
				(error) => {
					return console.log('watchLocation error', error);
				},
				() => {
					this.noPermission = false;
					localStorage.setItem('hasGeolocationPermission', true);
					console.log('watchLocation end');
				},
			);
		},
		setGeolocation(coordinates) {
			console.log('set geolocation');
			const getlocation = {
				latitude: coordinates.lat,
				longitude: coordinates.lng,
			};
			this.$store.commit('setGetlocation', getlocation);
		},
		goTo(selectedPath) {
			if (selectedPath === this.currentSelected) return;
			this.$store.commit('cleanList');
			this.currentSelected = selectedPath;
			this.$router.push({ name: selectedPath });
		},
	},
	mounted() {
		this.$store.commit('cleanList');
		if (localStorage.getItem('hasGeolocationPermission')) {
			this.noPermission = false;
			this.setCurrentLocation();
		}
		// console.log(this.$route.path);
		// console.log(this.$route);
	},
	computed: {
		...mapGetters(['getCurrentPath']),
	},
	watch: {
		getCurrentPath() {
			if (this.getCurrentPath) {
				this.currentSelected = this.getCurrentPath;
			}
		},
	},
};
</script>

<style lang="scss">
@import './styles/main.scss';
</style>
